.posts_container{
    border: 1px solid lightgray;
    
    background-color: white;
    border-radius: 10px;
    color: grey;
    padding-top: 5px;
    padding-left: 20px;
    margin: 20px;
    
}
.posts_header{
    display: flex;
}
.posts_name{
    font-size: 1rem;
    color: black;
    font-weight: bolder;
}
.posts_des{
    font-size: 0.8rem;

}
.posts_body > p{
    padding-left: 10px;
    padding-top: 15px;
}
.posts_info{
    margin-left: 15px;
}
.posts_options{
    display: flex;
}