.login_container > img{
    width: 150px;
    object-fit: contain;
}
.login_container{
    background-color: whitesmoke;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}
.login_container > form{
    display: flex;
    flex-direction: column;
    margin: 20px 0;;
}
.login_container >form >input{
    width: 365px;
    height: 50px;
    margin-bottom: 8px;
    border-radius: 5px;
    border-style: solid;
    border-width: 2px;
    outline: none;
}
.login_container >form >button{
    width: 371px;
    height: 50px;
    font-size: large;
    color: white;
    background-color: blue;
    cursor: pointer;
    border-radius: 5px;
}
.login_container >p{
    font-size: 0.8rem;
    font-weight: 400;
}
.login_container >p>span{
    cursor: pointer;
    color: blue;
}